import React, { useContext } from "react"
import {
  PostContainer,
  PostNum,
  PostBox,
  PostTitle,
  PostDate,
  PostDes,
} from "./Post.style"
import themeGroup from "../../theme/theme"
import { ThemeContext } from "../../theme/ThemeContext"
import { Link } from "gatsby"

type PostProps = {
  slug: string
  title: string
  description: string
  date: string
  link: string
}

const Post = ({ slug, title, description, date, link }: PostProps) => {
  const [themeMode] = useContext(ThemeContext)
  const theme = themeGroup[themeMode]
  const postNum = slug.split("/")[2]
  const num = postNum[postNum.length - 1]

  return (
    <div css={PostContainer}>
      <div css={PostNum(theme)}>{`-`}</div>
      <div css={PostBox}>
        <Link css={PostTitle(theme)} to={`/${link}/post-${num}`}>
          {title}
        </Link>
        <div css={PostDes(theme)}>{description}</div>
        <div css={PostDate(theme)}>작성일 : {date}</div>
      </div>
    </div>
  )
}

export default Post
